.app {
  font: 14px/1.4 Roboto, Helvetica, Arial, Tahoma, sans-serif;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 10px;
  width: 50vw;

  .hashtagsSect,
  .tweetsSect {
    border: 1px solid #bcc2c6;
    box-shadow: 8px 9px 5px -8px rgb(0 0 0 / 75%);
  }

  .searchSect,
  .tweetsSect {
    grid-column: 1 / span 3;
  }

  .searchSect {
    grid-row: 1 / span 1;
    padding: 20px 0;

    input {
      margin: 14px 0;
      width: 100%;
    }
  }

  .hashtagsSect {
    background-color: #fff;
    grid-row: 1 / span 2;
    grid-column: 4 / span 2;
    padding: 20px 20px 20px 10px;

    h4 {
      padding-left: 10px;
    }

    .hashtag {
      background-color: #e7f3fa;
      color: #3b94d9;
      display: inline-block;
      border-radius: 50px;
      margin: 15px 0 0 10px;
      padding: 10px 20px;
    }
  }

  .tweetsSect {
    float: left;
    grid-row: 2  / span 4;

    div {
      background-color: #fff;
    }

    div:nth-child(even) * {
      background-color: #f8f9f9;
    }

    .avatar {
      display: inline-block;
      padding: 10px 0 0 20px;
      vertical-align: top;
      width: 20%;

      img {
        border-radius: 50%;
      }
    }

    .tweetText {
      display: inline-block;
      padding: 20px;
      width: 80%;

      .user {
        font-weight: bold;
      }

      .link {
        color: #3b94d9;
        text-decoration: none;
        outline: 0;

        &:hover {
          color: #55acee;
        }
      }
    }
  }
}

/* Medium screens */
@media all and (max-width: 1024px) {
  #root {
    background-color: red;

    .app {
      .searchSect,
      .hashtagsSect,
      .tweetsSect {
        grid-column: 1 / span 5;
      }
    }
  }

}

/* Small screens */
@media all and (max-width: 500px) {
  #root {
    background-color: green;

    .app {
      width: 100vw;
    }
  }
}